/**
React Calendar Timeline CSS
 */

.react-calendar-timeline * {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box; }

.react-calendar-timeline .rct-outer {
	display: block;
	overflow: hidden;
	white-space: nowrap; }

.react-calendar-timeline .rct-scroll {
	display: inline-block;
	white-space: normal;
	vertical-align: top;
	overflow-x: scroll;
	overflow-y: hidden;
	-ms-touch-action: none;
	touch-action: none; }

.react-calendar-timeline .rct-canvas {
	position: relative; }

.react-calendar-timeline .rct-item {
	position: absolute;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	font-size: 13px;
	color: white;
	cursor: pointer;
	background: #2590b9;
	border: 1px solid #1a6fb3;
	border-right: 4px solid #1a6fb3;
	z-index: 80; }
.react-calendar-timeline .rct-item.selected {
	background: #ffc107;
	border: 1px solid #ff9800;
	z-index: 82; }
.react-calendar-timeline .rct-item.selected.can-move {
	cursor: move; }
.react-calendar-timeline .rct-item.selected.can-resize-left {
	border-left-width: 3px; }
.react-calendar-timeline .rct-item.selected.can-resize-left .rct-drag-left {
	cursor: w-resize; }
.react-calendar-timeline .rct-item.selected.can-resize-right {
	border-right-width: 3px; }
.react-calendar-timeline .rct-item.selected.can-resize-right .rct-drag-right {
	cursor: e-resize; }
.react-calendar-timeline .rct-item .rct-drag-left {
	position: absolute;
	width: 24px;
	max-width: 20%;
	min-width: 2px;
	height: 100%;
	top: 0;
	left: 0;
	cursor: pointer;
	z-index: 88; }
.react-calendar-timeline .rct-item .rct-drag-right {
	position: absolute;
	width: 24px;
	max-width: 20%;
	min-width: 2px;
	height: 100%;
	top: 0;
	right: 0;
	cursor: pointer;
	z-index: 88; }
.react-calendar-timeline .rct-item:hover {
	z-index: 88; }
.react-calendar-timeline .rct-item .rct-item-content {
	position: sticky;
	position: -webkit-sticky;
	left: 0px;
	overflow: visible;
	display: inline-block;
	border-radius: 2px;
	left: 0;
	padding: 0 6px;
	height: 100%;
	line-height: 1;
	padding: .2em;
}

.react-calendar-timeline .rct-header-container {
	z-index: 90;
	display: -ms-flexbox;
	display: flex; }
.react-calendar-timeline .rct-header-container.header-sticky {
	position: sticky;
	position: -webkit-sticky; }

.react-calendar-timeline .rct-header {
	margin: 0;
	overflow-x: hidden;
	z-index: 90; }
.react-calendar-timeline .rct-header .top-header,
.react-calendar-timeline .rct-header .bottom-header {
	position: relative; }
.react-calendar-timeline .rct-header .rct-label-group {
	padding: 0 5px;
	position: absolute;
	top: 0;
	font-size: 14px;
	text-align: center;
	cursor: pointer;
	border-left: 2px solid #bbb;
	color: #ffffff;
	background: #a663f1;
	border-bottom: 1px solid #bbb;
	cursor: pointer;
	font-weight: 600;
}
.react-calendar-timeline .rct-header .rct-label-group.rct-has-right-sidebar {
	border-right: 1px solid #bbb;
	border-left: 1px solid #bbb; }
.react-calendar-timeline .rct-header .rct-label-group > span {
	position: -webkit-sticky;
	position: sticky;
	left: 5px;
	right: 5px; }
.react-calendar-timeline .rct-header .rct-label {
	position: absolute;
	text-align: center;
	cursor: pointer;
	border-left: 1px solid #bbb;
	color: #333333;
	background: #f0f0f0;
	border-bottom: 1px solid #bbb;
	cursor: pointer;
}
.react-calendar-timeline .rct-header .rct-label.rct-label-only {
	color: #ffffff;
	background: #a663f1;
}
.react-calendar-timeline .rct-header .rct-label.rct-first-of-type {
	border-left: 2px solid #bbb; }

.react-calendar-timeline .rct-sidebar-header {
	margin: 0;
	color: #ffffff;
	background: #a663f1;
	border-right: 1px solid #bbb;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	border-bottom: 1px solid #bbb;
	overflow: hidden;
}
.react-calendar-timeline .rct-sidebar-header.rct-sidebar-right {
	border-right: 0;
	border-left: 1px solid #bbb; }

.react-calendar-timeline .rct-sidebar {
	overflow: hidden;
	white-space: normal;
	display: inline-block;
	vertical-align: top;
	position: relative;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	border-right: 1px solid #bbb; }
.react-calendar-timeline .rct-sidebar.rct-sidebar-right {
	border-right: 0;
	border-left: 1px solid #bbb; }
.react-calendar-timeline .rct-sidebar .rct-sidebar-row {
	padding: 0 4px;
	overflow: hidden;
	white-space: nowrap;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
	-webkit-box-sizing: content-box;
	        box-sizing: content-box;
	margin: 0;
	border-bottom: 1px solid #bbb; }
.react-calendar-timeline .rct-sidebar .rct-sidebar-row.rct-sidebar-row-odd {
	background: rgba(0, 0, 0, 0.05); }
.react-calendar-timeline .rct-sidebar .rct-sidebar-row.rct-sidebar-row-even {
	background: transparent; }

.react-calendar-timeline .rct-vertical-lines .rct-vl {
	position: absolute;
	border-left: 1px solid #bbb;
	background: #fff;
	z-index: 30; }
.react-calendar-timeline .rct-vertical-lines .rct-vl.rct-vl-first {
	border-left-width: 2px; }
.react-calendar-timeline .rct-vertical-lines .rct-vl.rct-day-6, .react-calendar-timeline .rct-vertical-lines .rct-vl.rct-day-0 {
	background: rgba(250, 246, 225, 0.5); }

.react-calendar-timeline .rct-horizontal-lines .rct-hl-even,
.react-calendar-timeline .rct-horizontal-lines .rct-hl-odd {
	border-bottom: 1px solid #bbb;
	-webkit-box-sizing: content-box;
	        box-sizing: content-box;
	position: absolute;
	z-index: 40; }

.react-calendar-timeline .rct-horizontal-lines .rct-hl-odd {
	background: rgba(0, 0, 0, 0.05); }

.react-calendar-timeline .rct-horizontal-lines .rct-hl-even {
	background: transparent; }

.react-calendar-timeline .rct-today {
	position: absolute;
	width: 2px;
	background: #00aa57;
	z-index: 50; }

.react-calendar-timeline .rct-cursor-line {
	position: absolute;
	width: 2px;
	background: #2196f3;
	z-index: 51; }

.react-calendar-timeline .rct-infolabel {
	position: fixed;
	left: 100px;
	bottom: 50px;
	background: rgba(0, 0, 0, 0.5);
	color: white;
	padding: 10px;
	font-size: 20px;
	border-radius: 5px;
	z-index: 85; }



